/*
 *  Default style for xterm.js
 */

.terminal {
    background-color: #000;
    color: #fff;
    font-family: courier-new, courier, monospace;
    font-feature-settings: "liga" 0;
    position: relative;
}

.terminal.focus,
.terminal:focus {
    outline: none;
}

.terminal .xterm-helpers {
    position: absolute;
    top: 0;
}

.terminal .xterm-helper-textarea {
    position: absolute;
    /*
     * HACK: to fix IE's blinking cursor
     * Move textarea out of the screen to the far left, so that the cursor is not visible.
     */
    left: -9999em;
    opacity: 0;
    width: 0;
    height: 0;
    z-index: -10;
}

.terminal .terminal-cursor {
    background-color: #fff;
    color: #000;
}

.terminal:not(.focus) .terminal-cursor {
    outline: 1px solid #fff;
    outline-offset: -1px;
    background-color: transparent;
}

.terminal.focus .terminal-cursor.blinking {
    animation: blink-cursor 1.2s infinite step-end;
}

@keyframes blink-cursor {
    0% {
        background-color: #fff;
        color: #000;
    }
    50% {
        background-color: transparent;
        color: #FFF;
    }
}

.terminal .composition-view {
    background: #000;
    color: #FFF;
    display: none;
    position: absolute;
    white-space: nowrap;
    z-index: 1;
}

.terminal .composition-view.active {
    display: block;
}

.terminal .xterm-viewport {
    /* On OS X this is required in order for the scroll bar to appear fully opaque */
    background-color: #000;
    overflow-y: scroll;
}

.terminal .xterm-rows {
    position: absolute;
    left: 0;
    top: 0;
}

.terminal .xterm-scroll-area {
    visibility: hidden;
}

.terminal .xterm-char-measure-element {
    display: inline-block;
    visibility: hidden;
    position: absolute;
    left: -9999em;
}

/*
 *  Determine default colors for xterm.js
 */
.terminal .xterm-bold {
    font-weight: bold;
}

.terminal .xterm-underline {
    text-decoration: underline;
}

.terminal .xterm-blink {
    text-decoration: blink;
}

.terminal .xterm-hidden {
